import React, {Component} from 'react';

import {getTempo} from './audio/getTempo';
import {getWaveform} from './audio/getWaveform';
import tapTempo from 'tap-tempo';

import FigureOne from './components/FigureOne';

import styles from './styles/main.css';

const tapCounter = tapTempo();
const modeDescriptions = {
	listen: (analyzingTempo, mode) =>  analyzingTempo && mode === 'listen' && 'analyzing tempo...',
	tap: () => 'tap the spacebar'
};

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {
			tempo: 110,
			mode: 'listen',
			analyzingTempo: false
		};

		this.clearWaveform = this.clearWaveform.bind(this);
		this.getWaveform = this.getWaveform.bind(this);
		this.handleKeyDown = this.handleKeyDown.bind(this);
		this.initiateListenTempo = this.initiateListenTempo.bind(this);
		this.initiateTapTempo = this.initiateTapTempo.bind(this);
		this.pollForTempo = this.pollForTempo.bind(this);

		this._drawInterval;
		this._waveform;
	}

	componentDidMount() {
		window.addEventListener('keydown', this.handleKeyDown);
		tapCounter.on('tempo', tempo => this.setState({tempo}));
		this.pollForTempo();
	}

	componentWillUnmount() {
		window.removeEventListener('keydown', this.handleKeyDown);
	}

	clearWaveform() {
		if (!this._waveform) return;
		const canvas = this._waveform.getContext('2d');
		canvas.clearRect(0, 0, this._waveform.width, this._waveform.height);
		clearInterval(this._drawInterval);
	}

	getWaveform() {
		this.clearWaveform();

		getWaveform(this._waveform, this._drawInterval)
			.then(drawInterval => this._drawInterval = drawInterval)
			.catch(err => console.log(err));
	}

	handleKeyDown(event) {
		if (this.state.mode === 'tap' && event.keyCode === 32) {
			event.preventDefault();
			tapCounter.tap();
		}
	}

	pollForTempo() {
		this.setState({analyzingTempo: true}, () => {
			return getTempo(10)
				.then(({tempo}) => {
					this.state.mode === 'listen' &&
						this.setState({tempo, analyzingTempo: false}, this.getWaveform);
				})
				.catch(err => {
					this.state.mode === 'listen' &&
						this.pollForTempo();
				});
		});
	}

	initiateTapTempo() {
		this.setState({mode: 'tap', tempo: 0}, () => this.clearWaveform());
	}

	initiateListenTempo() {
		this.setState({mode: 'listen', tempo: 0}, () => this.pollForTempo());
	}

	render() {
		return (
			<div className={styles.container}>
			<div className={styles.linkContainer}>
				<a href="https://soundcloud.com/motionnmaking" target="_blank">soundcloud</a>
			</div>
			<div className={styles.infoContainer}>
				<div className={styles.buttonWrapper}>
					<button
						className={this.state.mode === 'tap' ? styles.buttonActive : styles.button}
						onClick={this.initiateTapTempo}>
						tap
					</button>
					<button
						className={this.state.mode === 'listen' ? styles.buttonActive : styles.button}
						onClick={this.initiateListenTempo}>
						listen
					</button>
				</div>
				<div className={styles.modeDescription}>
					{modeDescriptions[this.state.mode](this.state.analyzingTempo, this.state.mode)}
				</div>
			</div>
				<FigureOne tempo={this.state.tempo}/>
				<canvas className={styles.waveform} ref={canvas => this._waveform = canvas}></canvas>
			</div>
		);
	}
}

export default App;
